import axios from 'axios'
import Utils from '@/assets/js/utils'
import MockAdapter from 'axios-mock-adapter'
import { UserList, MenuList, BannerList, Destinations, HotImgList, HotSmallImgList,
	NationwideLargeImgList, NationwideImgList, OverseasList, SamplesImgList, WareList, 
	RecommendList, WareImgList, Banners, ShowImgList } from './data/data'
let _UserList = UserList;
const retObj = {
	code: '0001',
	message: '',
	result: {}
}
export default {
	bootstrap() {
		let mock = new MockAdapter(axios)
		// 
		mock.onPost('/login').reply(config => {
			let { username, password, isAdmin } = JSON.parse(config.data);
			let loginUser = _UserList.filter(user => user.email === username && 
				user.isAdmin === isAdmin)[0];
			if(loginUser) {
				if(loginUser.password === password) {
					return new Promise((resolve, reject) => {
						setTimeout(() => {
							retObj.result.userInfo = loginUser;
							resolve([200, retObj])
						}, 1000)
					})
				} else {
					let retObj = {
						code: '1002',
						message: ''
					}
					return new Promise((resolve, reject) => {
						setTimeout(() => {
							resolve([200, retObj])
						}, 1000)
					})
				}
			} else {
				let retObj = {
					code: '1001',
					message: ''
				}
				return new Promise((resolve, reject) => {
					setTimeout(() => {
						resolve([200, retObj])
					}, 1000)
				})
			}
		})
		// 
		mock.onGet('/logout').reply(config => {
			Utils.delCookie('userId')
			retObj.result = {}
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					resolve([200, retObj])
				}, 500)
			})
		})
		// 
		mock.onGet('/showInter/getMyInfo.do').reply(config => {
			let userId = Utils.getCookie('userId');
			let retObj = {
				code: '0001',
				message: '',
				result: {}
			}
			if(!userId) {
				return new Promise((resolve, reject) => {
					retObj.code = '0002';
					retObj.message = 'Session';
					setTimeout(() => {
						resolve([200, retObj])
					}, 500)
				})
			}
			let _userInfo = _UserList.filter(user => user.userId == userId)[0]
			retObj.result = {
				userInfo: _userInfo
			}
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					resolve([200, retObj])
				}, 500)
			})
		})
		// 
		mock.onGet('/mall/getMenus.do').reply(config => {
			let retObj = {
				code: '0001',
				message: '',
				result: {
					menuList: MenuList
				}
			}
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					resolve([200, retObj])
				}, 0)
			})
		})
		// Banner
		mock.onGet('/mall/getIndexBanner.do').reply(config => {
			let retObj = {
				code: '0001',
				message: '',
				result: {
					bannerList: Banners
				}
			}
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					resolve([200, retObj])
				}, 0)
			})
		})
		// 
		mock.onGet('/mall/getDestinations.do').reply(config => {
			let retObj = {
				code: '0001',
				message: '',
				result: {
					destinations: Destinations
				}
			}
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					resolve([200, retObj])
				}, 0)
			})
		})
		// ImgList
		mock.onGet('/mall/getImgList.do').reply(config => {
			let retObj = {
				code: '0001',
				message: '',
				result: {
					hotImgList: HotImgList,
					hotSmallImgList: HotSmallImgList,
					nationwideImgList: NationwideImgList,
					nationwideLargeImgList: NationwideLargeImgList,
					overseasList: OverseasList,
					samplesImgList: SamplesImgList,
				}
			}
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					resolve([200, retObj])
				}, 0)
			})
		})
		// WareList
		mock.onGet('/mall/getWares.do').reply(config => {
			let retObj = {
				code: '0001',
				message: '',
				result: {
					wareList: WareList
				}
			}
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					resolve([200, retObj])
				}, 0)
			})
		})
		// RecommendList
		mock.onGet('/mall/getRecommends.do').reply(config => {
			let retObj = {
				code: '0001',
				message: '',
				result: {
					recommendList: RecommendList
				}
			}
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					resolve([200, retObj])
				}, 0)
			})
		})
		// WareImgList
		mock.onGet('/mall/getWareImgs.do').reply(config => {
			let retObj = {
				code: '0001',
				message: '',
				result: {
					wareImgList: WareImgList
				}
			}
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					resolve([200, retObj])
				}, 0)
			})
		})
		// 
		mock.onGet('/showInter/getImageList.do').reply(config => {
			let { city } = config.params;
			console.log(city)
			let retObj = {
				code: '0001',
				message: '',
				result: {},
			}
			console.log(ShowImgList)
			if(city && ShowImgList[city]) {
				retObj.result = {
					imageList: ShowImgList[city]
				}
			} else {
				retObj.code = '0002',
				retObj.message = ''
			}
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					resolve([200, retObj])
				}, 0)
			})
		})
	}
}


