<template>
	<header class="header">
		<div class="topbar">
			<div class="container">
				<h3>-</h3>
			</div>
		</div>
		<div class="headerbar">
			<div class="container">
				<div class="header-logo">
					<router-link to="/" class="logo">logo</router-link>
				</div>
				<div class="header-link">
					<div v-if="userInfo" class="user-info">
						<i class="icon user"></i>
						<router-link to="/">{{ userInfo.name }}</router-link>
						<em>|</em>
						<span @click="handleExit"></span>
					</div>
					<div v-else class="login">
						<a href="http://ums.yueshijue.com/ums/#/register" class="btn-signup"></a>
						<a href="http://ums.yueshijue.com/cas/login?service=http://b2b.yueshijue.com/lvpai/portal/single/pclogin" class="btn-signin"></a>
					</div>
				</div>
				<div class="header-search">
					<form action="" class="search-form">
						<input type="text" v-model.trim="search" class="search-input" :placeholder="placeholder" autofocus="autofocus"></input>
						<i class="el-icon-search" @click="handleSearch"></i>
					</form>
				</div>
			</div>
		</div>
		<div class="navbar">
			<div class="container">
				<ul class="header-nav">
					<li><a href="http://b2b.yueshijue.com/index.html"></a></li>
					<li>
						<a href="http://b2b.yueshijue.com/productList.html" class="relative">
							
						</a>
					</li>
					<li><a href="http://b2b.yueshijue.com/productList.html"></a></li>
					<li><a href="http://b2b.yueshijue.com/productList.html"></a></li>
					<li><a href="http://b2b.yueshijue.com/productList.html"></a></li>
					<li><router-link to="/"></router-link></li>
					<li><router-link to="/show" class="active"></router-link></li>
					<li><a href="http://b2b.yueshijue.com/aboutUs.html"></a></li>
				</ul>
				<div class="header-order">
					<router-link to="/" class="my-order"></router-link>	
				</div>
			</div>
		</div>
	</header>
</template>
<script>
	import { requestExit } from '@/api'
	export default {
		data() {
			return {
				placeholder: '',
				search: '',
				navBarTop: 0,
			}
		},
		props: ['userInfo'],
		methods: {
			handleSearch() {
				if(!this.search) return;
				window.location.href = 'http://b2b.yueshijue.com/productList.html?keycode='+ this.search;
				// this.$notify({
				// 	type: 'warning',
				// 	title: '',
				// 	message: `${this.search}`
				// })
			},
			handleExit() {
				this.$confirm('', '', {type: 'warning'}).then(() => {
						requestExit().then(res => {
							if(res.data.code === '0001') {
								this.$router.replace( '/login')
							} else {
								this.$message.error(res.data.message)
							}
						})
				}).catch(err => {
					console.log(err)
				})
			},
			enterSearch(e) {
				e.keyCode === 13 && this.handleSearch()
			},
			scroll() {
				let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
				if(scrollTop >= this.navBarTop) {
					document.querySelector('.navbar').className = 'navbar fixed';
				} else {
					document.querySelector('.navbar').className = 'navbar';
				}
			},
		},
		mounted() {
			this.navBarTop = document.querySelector('.navbar').offsetTop || 0;
			document.querySelector('.search-input').focus(); 
			document.querySelector('.search-input').addEventListener('keydown', this.enterSearch)
		},
	  beforeDestroy() {
	  	document.removeEventListener('scroll', this.scroll)
	  },
	}
</script>
<style scoped lang="scss">
	.header {
		.topbar {
			background: #f0f0f0;
			h3 {
				padding: 8px 0;
				color: #999;
				font-size: 14px;
				line-height: 1;
				text-align: left;
				letter-spacing: 1px;
			}
		}
		.headerbar {
			line-height: 70px;
			background: #fff;
			.header-logo {
				float: left;
				height: 40px;
				margin-top: 13px;
				.logo {
					display: inline-block;
					width: 120px;
					height: 43px;
					text-indent: -2000em;
					background: url(../assets/img/logo_blue.png) no-repeat center;
					background-size: cover;
				}
			}
			.header-search {
				float: right;
				margin-right: 50px;
				width: 300px;
				height: 36px;
				margin-top: 17px;
				line-height: 36px;
				padding-left: 12px;
				border: 1px solid #00AAEF;
				.search-input {
					width: 255px;
					height: 32px;
					font-size: 16px;
					border: none;
					background: transparent;
				}
				.el-icon-search {
					position: relative;
					right: -5px;
					cursor: pointer;
					color: #999;
				}
			}
			.header-link {
				float: right;
				line-height: 70px;
				color: #fff;
				font-size: 16px;
				a[class^="btn-"] {
					display: inline-block;
					width: 100px;
					height: 30px;
					line-height: 30px;
					text-align: center;
					font-size: 14px;
					vertical-align: middle;
				}
				.btn-signin {
					color: #fff;
					background: #00AAEF;
				}
				.btn-signup {
					color: #00AAEF;
					border: 1px solid #00AAEF;
					&:hover {
						color: #fff;
						background: #00AAEF;
					}
				}
				.user-info {
					color: #333;
					.user {
				    width: 21px;
				    height: 22px;
				    vertical-align: middle;
				    margin-top: -5px;
				    margin-right: 5px;
						background-position: 2px 0;
					}
					a {
						color: #333;
						font-size: 16px;
						vertical-align: middle;
					}
					em {
						margin: 0 5px;
					}
					span {
						cursor: pointer;
					}
				}
			}
		}
		.navbar {
			background: #00AAEF;
			&.fixed {
				position: fixed;
				top: 0;
				z-index: 9999;
				width: 100%;
				background: rgba(0,170,239,.8);
				box-shadow: 1px 1px 3px 3px rgba(0,0,0,.2);
			}
			.header-nav {
				float: left;
				line-height: 50px;
				li {
					float: left;
					margin-left: 30px;
					font-size: 18px;
					&:first-child {
						margin-left: 0;
					}
					a {
						color: #fff;
						transition: all .3s;
						&:hover, &.active {
	  					padding-bottom: 6px;
							border-bottom: 2px #EFE701 solid;
						}
						.tag {
							position: absolute;
							top: -25px;
							right: -10px;
						}
					}
				}
			}
			.header-order {
				float: right;
				line-height: 50px;
				.my-order {
					display: block;
					padding: 0 70px;
					color: #fff;
					font-size: 16px;
					background: #56CEFF;
				}
			}
		}
		.header-left {
			float: left;
		}
		.header-right {
			float: right;
			height: 47px;
			padding-top: 8px;
		}
		.shopping-cart {
			float: left;
			margin: 0 40px;
			font-size: 24px;
			color: #fff;
			.cart {
			  width: 29px;
			  height: 20px;
			  cursor: pointer;
				background-position: -151px 0;
				&:hover {
					background-position: -121px 0;
				}
			}
		}
	}
</style>


